
const Index = (props: any) => {
  const [clients, setClient] = useState([]);

  useEffect(() => {
    setClient(props.data);
  });

  if (clients.length === 0){
    return "Loading";
  }
  return (
    <div>
      <h1>List of all clients</h1>
      <ul>{clients.map(e => <li key={e._id}>{e.name}, {e.age}, {e.address}</li>)}</ul>
    </div>
  );
};



export default Index;
